import * as React from 'react';
import { Grid,TextField,} from '@material-ui/core/';
import { useState } from 'react';
import { HistoryTable} from './HistoryTable'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch } from '@fortawesome/free-solid-svg-icons';

//import { TableComponent } from 'Components/UI/TableComponent';
// import { ImageViewer } from '../../../Components/UI/ImageViewer'; 
import { Loader } from 'Components/UI/LoadingIndicator';
interface HistoryProps {
}
let filtersForApi = {
    where: {}
  }
let limit = 10;
let order = {}
let currentSortAttr = "";
export const HistorySection: React.FC<HistoryProps> = (
): JSX.Element => {
  //const [memberId, setMemberId] = useState(props.memberId);
  const [page, setPage] = useState(0);
  //const [totalCount, setTotalCount] = useState(4)
  const [defaultFilters , setDefaultFilters] = useState<any[]>([]);
  const [renderTable,setRenderTable] = useState(true)
  const [typingTimeout, setTypingTimeout] = useState<any>(0);
  const [loading, setLoading] = useState(false);
  const [sorting, setSorting] = useState<any[]>([]);
  const [HistoryListTable,setHistoryListTable] = useState<any>()
  const [largeSearch , setLargeSearch] = useState('');
  //useEffect(() => {
  //  setMemberId(props.memberId);
  //}, [props.memberId]);
    console.log(order,currentSortAttr);
  const historylistcolumn: any = [
    { 
        name: 'contract', 
        title: 'Contract'
    },
    {
        name: 'effectivity_date', 
        title: 'Contract Effectivity Date'
    },
    {
        name: 'plan_type', 
        title: 'Plan Type', 
    },
    {
        name: 'type', 
        title: 'Type'
    },
    {
        name: 'status', 
        title: 'Status', 
    },
    ];
    const columnExtensions = [
        {
          columnName: 'contract',
          wordWrapEnabled: true,
          textAlign:'center',
        },
        {
          columnName: 'effectivity_date',
          wordWrapEnabled: true,
          textAlign:'center',
        },
        {
          columnName: 'plan_type',
          wordWrapEnabled: true,
          textAlign:'center',
        },
        {
          columnName: 'type',
          wordWrapEnabled: true,
          textAlign:'center',
        },
        {
          columnName: 'status',
          wordWrapEnabled: true,
          textAlign:'center',
        },
    ];
    const rows = [
        {
            contract:'Samp',
            effectivity_date:'EffSamp',
            plan_type:'planSamp',
            type:'typeSamp',
            status:'statusSamp',
        },
        {
            contract:'Samp2',
            effectivity_date:'EffSamp2wtwertwertwertwtwetrwtwertwrtrwertw',
            plan_type:'planSamp2',
            type:'typeSamp2',
            status:'statusSamp2',
        },
        {
            contract:'Samp3',
            effectivity_date:'EffSamp3',
            plan_type:'planSamp3',
            type:'typeSamp3',
            status:'statusSamp3',
        },
        {
            contract:'Samp3',
            effectivity_date:'EffSamp3',
            plan_type:'planSamp3',
            type:'typeSamp3',
            status:'statusSamp3',
        },
    ]
    const handlePageChange = (currentPage: number, skip: number) => {
        setPage(currentPage);
        console.log('handle page change')
        filtersForApi['skip']=skip;
        //handleLoadProposalsFromAPI(limit, skip);
      }
    const handlePageSizeChange = (value) => {
        limit = value;
    
        //handleLoadProposalsFromAPI(limit, 0);
    }
    const handleFilterByColumn = (filter: any) => {
        console.log('Filter By Column: ',filter);
        setDefaultFilters(filter);
        setPage(0);
        if(filter.length>0)
        {
          let tmpfilter = {};
          let value:any;
          filter.map(fil =>{
            let field = fil.columnName;
            if(fil.columnName == 'contract'){
              field = "contract";
              value = {like: fil.value, options: 'i'};
            }else if(fil.columnName == 'effectivity_date'){
              field = "effectivity_date";
              tmpfilter[field]={
                lte:new Date(fil.value)
              }
              field = "effectivity_date";
              value = new Date(fil.value)
            } else if(fil.columnName == 'plan_type'){
              field = "plan_type";
              value = {like: fil.value, options: 'i'};
            } else if(fil.columnName == 'type'){
              field = "type";
              value = {like: fil.value, options: 'i'};
            }
            else if(fil.columnName == 'version'){
              field = "version";
              value = {like: fil.value, options: 'i'};
            }else if(fil.columnName == 'status'){
              field = "status";
              value = {like: fil.value, options: 'i'};
            }
            //tmpfilter[field] = {like:fil.value, options:'i'};
            if(fil.columnName == 'effectivity_date')
            {
              tmpfilter[field] = {
                gte : value
              }
            } else if(fil.columnName === 'date_modified' || fil.columnName === 'date_created'){
              if(tmpfilter[field])
              {tmpfilter[field]=tmpfilter[field].concat(value);}
              else{
                tmpfilter[field] = value
              }
            }
            else
            {
              tmpfilter[field] = value
            }
          })
          filtersForApi['where'] = tmpfilter;
        }
        else
        {
          filtersForApi['where']={};
        }
        setRenderTable(true); 
        if (typingTimeout) {
          clearTimeout(typingTimeout);
        }
    
        setTypingTimeout (setTimeout(function () {
                setLoading(true);
                //handleLoadProposalsFromAPI(limit, 0);
              }, 2000)
        );
      }
      const handleSearch = (value: string | null) => {
        console.log(value);
        setPage(0);
    
        if (typingTimeout) {
          clearTimeout(typingTimeout);
        }
    
        setTypingTimeout (setTimeout(function () {
                setLoading(true);
                //handleLoadProposalsFromAPI(limit, 0);
              }, 2000)
        );
      }
      const handleFiltersOnChange = () => {
        if (typingTimeout) {
          clearTimeout(typingTimeout);
        }
    
        setTypingTimeout (setTimeout(function () {
                setLoading(true);

                //handleLoadProposalsFromAPI(limit, 0);
              }, 2000)
        );
      }
      const handleSortChange = (sort: any) => {
        setSorting(sort);
        console.log('handle sort:',sort)
        let mask = "";
        if(sort[0].columnName == 'account'){
          mask = "client.registered_name";
        }else if(sort[0].columnName == 'proposal_no'){
          mask = "proposal_number";
        }else if(sort[0].columnName == 'effectivity_date'){
          mask = "custom_metadata.coverage_start_date";
        } else if(sort[0].columnName == 'version'){
          mask = "version";
        }else if(sort[0].columnName == 'date_created'){
          mask = "custom_metadata.date_created";
        }else if(sort[0].columnName == 'date_modified'){
          mask = "custom_metadata.date_updated";
        }else if(sort[0].columnName == 'status'){
          mask = "status";
        }else if(sort[0].columnName == 'bdobroker'){
          mask = "client.sales_channel.contact_person";
        }
  
        order = {
          field:mask,
          direction:(sort[0].direction === 'asc'? 1 : -1)
        };
        //handleLoadProposalsFromAPI(10, 0);
        currentSortAttr = mask;
      }
      const handleRowClick = (row: number) => {
        console.log(row);
      }
      const handleAssignTable =()=>{
        return(
            <HistoryTable
              columns={historylistcolumn}
              rows={rows}
              columnExtensions={columnExtensions}
              currentPage={page}
              limit={limit}
              totalCount={4}
              onPageChange={handlePageChange}
              onPageSizeChange={handlePageSizeChange}
              onFilterChange={handleFilterByColumn}
              onSearchChange={handleSearch}
              onSortingChange={handleSortChange}
              sorting={sorting}
              defaultFilter={defaultFilters}
              onRowClick={handleRowClick}
            />
        )}
      if(renderTable){
        setHistoryListTable(handleAssignTable());
        setRenderTable(false);
      }
      
      
  return (
    <Grid container>
        {loading ? <Loader /> : null}
        <Grid item xs={12} style={{paddingLeft: '24px'}}>
          <TextField
              id="proposal_search"
              placeholder="Search year, company, or contract"
              style={{backgroundColor: 'white',}}
              margin="dense"
              variant="outlined"
              fullWidth
              value={largeSearch}
              onChange={(event:any)=>{
                  setLargeSearch(event.target.value)
                  let search_value = event.target.value.trim(); 
                  setRenderTable(true)
                  if(filtersForApi['where']){
                    filtersForApi['where']['or'] = [
                      {"client.registered_name": {like: search_value, options: 'i'}},
                      {status: {like: search_value, options: 'i'}},
                      {proposal_number: {like: search_value, options: 'i'}},
                      {"client.sales_channel.contact_person": {like: search_value, options: 'i'}},
                      /*{"custom_metadata.coverage_start_date": {like: search_value, options: 'i'}},
                      {"custom_metadata.coverage_end_date": {like: search_value, options: 'i'}},
                      {"custom_metadata.date_created": {like: search_value, options: 'i'}},
                      {"custom_metadata.date_updated": {like: search_value, options: 'i'}},*/
                      {"custom_metadata.version": {like: search_value, options: 'i'}},
                    ];
                  }else{
                    filtersForApi['where'] = {};
                    filtersForApi['where']['or'] = [
                      {"client.registered_name": {like: search_value, options: 'i'}},
                      {status: {like: search_value, options: 'i'}},
                      {proposal_number: {like: search_value, options: 'i'}},
                      {"client.sales_channel.contact_person": {like: search_value, options: 'i'}},
                      /*{"custom_metadata.coverage_start_date": {like: search_value, options: 'i'}},
                      {"custom_metadata.coverage_end_date": {like: search_value, options: 'i'}},
                      {"custom_metadata.date_created": {like: search_value, options: 'i'}},
                      {"custom_metadata.date_updated": {like: search_value, options: 'i'}},*/
                      {"custom_metadata.version": {like: search_value, options: 'i'}},
                    ];
                  }
                  handleFiltersOnChange(); 
                } 
              }
              inputProps={{ 'aria-label': 'bare' }}
              InputProps={{
              startAdornment: (
                  <FontAwesomeIcon
                  icon={faSearch}
                  style={{color: '#00000061',
                  marginRight: '8px',}}
                  />
              ),
              }}
          />
        </Grid>
        
      <Grid item xs={12} style={{ boxShadow: '0px 3px 6px #00000029',marginTop:'20px',marginLeft: '24px' }}>
         {HistoryListTable}
        </Grid>
    </Grid>
  );
};

