interface HMOFieldItemProps {
  name?: string;
  value: string;
  disabled?: boolean;
  handleChange?: any;
  error?: boolean;
}

const HMOFieldItem: React.FC<HMOFieldItemProps> = (
  props: HMOFieldItemProps,
): JSX.Element => {
  const classes = hmofielditemStyles(props);

  let errorMsg = props.error ? props.name + ' is required/invalid' : '';

  return (
    <Grid
      container
      alignItems="flex-start"
      style={{ paddingTop: 12, paddingBottom: 12, paddingRight: 24 }}
    >
      <Grid item xs={12}>
        <Typography
          className={
            props.disabled ? classes.disabledhmoname : classes.enabledhmoname
          }
        >
          {props.name}
        </Typography>
      </Grid>
      <Grid item xs={12}>
        <InputTextField
          id={props.name ? props.name.toLowerCase().replace(' ', '-') : ''}
          disabled={props.disabled}
          onChange={props.handleChange}
          error={props.error}
          errorMessage={errorMsg}
          value={props.value}
        />
      </Grid>
    </Grid>
  );
};

HMOFieldItem.defaultProps = {
  disabled: false,
  name: '',
  value: '',
  error: false,
  handleChange: function() {},
};

