interface HMOPickerItemProps {
  name: string;
  value: Date;
  error: boolean;
  disabled?: boolean;
  handleChange?: any;
}

const HMOPickerItem: React.FC<HMOPickerItemProps> = (
  props: HMOPickerItemProps,
): JSX.Element => {
  const classes = hmopickeritemStyles(props);

  let errorMsg = props.error ? props.name + ' is required/invalid' : '';

  return (
    <Grid
      container
      alignItems="flex-start"
      style={{ paddingTop: 12, paddingBottom: 4, paddingRight: 24 }}
    >
      <Grid item xs={12}>
        <Typography
          className={
            props.disabled ? classes.disabledhmoname : classes.enabledhmoname
          }
        >
          {props.name}
        </Typography>
      </Grid>
      <Grid item xs={12}>
        <MuiPickersUtilsProvider utils={MomentUtils}>
          <KeyboardDatePicker
            id={props.name.split(' ').join('-')}
            className={
              props.error ? 'edithmo-date-picker-error' : 'edithmo-date-picker'
            }
            variant="inline"
            format="MM/DD/YYYY"
            value={props.value}
            onChange={(date: MaterialUiPickersDate | null) => {
              props.handleChange(date ? date.toDate() : null);
            }}
          />
        </MuiPickersUtilsProvider>
        {props.error && (
          <div>
            <FormHelperText className={classes.error}>
              {errorMsg}
              <ErrorIcon className={classes.errorIcon} />
            </FormHelperText>
          </div>
        )}
      </Grid>
    </Grid>
  );
};

interface HMOClientFieldProps {
  label: string;
  fieldname: string;
  value: any;
  type: string;
  disabled?: boolean;
  handleChange?: any;
  error: boolean;
}

const HMOClientField: React.FC<HMOClientFieldProps> = (
  props: HMOClientFieldProps,
): JSX.Element => {
  if (props.type === 'date') {
    return (
      <HMOPickerItem
        disabled={props.disabled}
        name={props.label}
        error={props.error}
        value={props.value ? props.value : null}
        handleChange={value => {
          props.handleChange(value ? value.toString() : '');
        }}
      />
    );
  } else {
    return (
      <HMOFieldItem
        disabled={props.disabled}
        error={props.error}
        name={props.label}
        value={props.value ? props.value : ''}
        handleChange={value => {
          props.handleChange(value ? value.toString() : '');
        }}
      />
    );
  }
};

/**
 * main page
 */

const ordinal_suffix_of = i => {
  var j = i % 10,
    k = i % 100;
  if (j === 1 && k !== 11) {
    return i + 'st';
  }
  if (j === 2 && k !== 12) {
    return i + 'nd';
  }
  if (j === 3 && k !== 13) {
    return i + 'rd';
  }
  return i + 'th';
};

