interface EditHMOInformationProps {
  memberData?: any;
  clientData?: any;
  contractName?: any;
  editMemberValues: any;
  clientFields: any[];
  errorFields: string[];
  dependentsErrorFields: any;
  MBL?: string;
  ABL?: string;
  PEC?: string;
  plan_type_options: ISelectItem[];
  handleTextChange: any;
  handleSelectChange: any;
  handleCheckboxChange: any;
  handleDependentsChange: any;
  handleClientFieldChange: any;
}

export const EditHMOInformation: React.FC<EditHMOInformationProps> = (
  props: EditHMOInformationProps,
): JSX.Element => {
  const classes = hmoinfoStyles(props);
  const {
    memberData,
    clientData,
    contractName,
    editMemberValues,
    clientFields,
    errorFields,
    dependentsErrorFields,
    plan_type_options,
    MBL,
    ABL,
    PEC,
  } = props;

  let dependents = editMemberValues['dependents'];
  let dependentForms = Object.keys(dependents).map(key => {
    let dependent = dependents[key];
    let dependentEF =
      dependentsErrorFields && dependentsErrorFields[key]
        ? dependentsErrorFields[key]
        : [];

    return (
      <ExpansionPanel key={key} style={{ boxShadow: 'none' }}>
        <ExpansionPanelSummary
          className={clsx(
            dependent['index'] % 2
              ? 'em-dependent-odd-summary'
              : 'em-dependent-even-summary',
          )}
          id={'panel' + dependent['index'] + '-header'}
          aria-controls={'panel' + dependent['index'] + '-content'}
          expandIcon={<ExpandMoreIcon />}
        >
          <Typography className={clsx('em-dependent-header')}>
            {ordinal_suffix_of(dependent['index'] + 1) + ' Dependent'}
          </Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Grid
            container
            style={{ paddingLeft: 16, paddingRight: 16, paddingBottom: 24 }}
          >
            <Grid item xs={12} md={3}>
              <HMOFieldItem
                name="First Name"
                value={dependent['first_name'] ? dependent['first_name'] : ''}
                error={dependentEF.includes('first_name')}
                handleChange={value => {
                  props.handleDependentsChange(key, 'first_name', value);
                }}
              />
              <HMOSelectItem
                name="Suffix"
                value={dependent['suffix']}
                options={suffix_options}
                handleChange={item => {
                  props.handleDependentsChange(key, 'suffix', item.value);
                }}
              />
            </Grid>
            <Grid item xs={12} md={3}>
              <HMOFieldItem
                name="Middle Name"
                value={dependent['middle_name'] ? dependent['middle_name'] : ''}
                error={dependentEF.includes('middle_name')}
                handleChange={value => {
                  props.handleDependentsChange(key, 'middle_name', value);
                }}
              />
              <HMOPickerItem
                name="Birthday"
                value={dependent['date_of_birth']}
                error={dependentEF.includes('date_of_birth')}
                handleChange={bd => {
                  props.handleDependentsChange(
                    key,
                    'date_of_birth',
                    bd ? bd.toString() : '',
                  );
                }}
              />
            </Grid>
            <Grid item xs={12} md={3}>
              <HMOFieldItem
                name="Last Name"
                value={dependent['last_name'] ? dependent['last_name'] : ''}
                error={dependentEF.includes('last_name')}
                handleChange={value => {
                  props.handleDependentsChange(key, 'last_name', value);
                }}
              />
              <HMOSelectItem
                name="Civil Status"
                value={dependent['civil_status']}
                options={cs_options}
                handleChange={item => {
                  props.handleDependentsChange(key, 'civil_status', item.value);
                }}
              />
            </Grid>
            <Grid item xs={12} md={3}>
              <HMOFieldItem
                name="Title"
                value={dependent['title']}
                handleChange={value => {
                  props.handleDependentsChange(key, 'title', value);
                }}
              />
              <HMOSelectItem
                name="Gender"
                value={dependent['gender']}
                options={gender_options}
                handleChange={item => {
                  props.handleDependentsChange(key, 'gender', item.value);
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <HMOSelectItem
                name="Relationship to Principal"
                value={dependent['relationship_to_principal']}
                options={rtp_options}
                handleChange={item => {
                  props.handleDependentsChange(
                    key,
                    'relationship_to_principal',
                    item.value,
                  );
                }}
              />
            </Grid>
          </Grid>
        </ExpansionPanelDetails>
      </ExpansionPanel>
    );
  });

  let columnOneFields: any[] = [];
  let columnTwoFields: any[] = [];
  let columnThreeFields: any[] = [];
  if (clientFields && clientFields.length > 0) {
    clientFields.forEach((item, index) => {
      if (
        item.field_name === 'registered_name' ||
        item.field_name === 'name_on_card'
      ) {
        switch (index % 3) {
          case 0:
            columnOneFields.push(
              <HMOFieldItem
                disabled={true}
                name={item.system_name}
                value={
                  clientData && clientData[item.field_name]
                    ? clientData[item.field_name]
                    : ''
                }
              />,
            );
            break;
          case 1:
            columnTwoFields.push(
              <HMOFieldItem
                disabled={true}
                name={item.system_name}
                value={
                  clientData && clientData[item.field_name]
                    ? clientData[item.field_name]
                    : ''
                }
              />,
            );
            break;
          case 2:
            columnThreeFields.push(
              <HMOFieldItem
                disabled={true}
                name={item.system_name}
                value={
                  clientData && clientData[item.field_name]
                    ? clientData[item.field_name]
                    : ''
                }
              />,
            );
            break;
          default:
            break;
        }
      } else {
        switch (index % 3) {
          case 0:
            columnOneFields.push(
              <HMOClientField
                label={item.system_name}
                fieldname={item.field_name}
                value={
                  editMemberValues && editMemberValues[item.field_name]
                    ? editMemberValues[item.field_name]
                    : ''
                }
                type={item.input_type}
                disabled={!item.is_editable}
                error={errorFields && errorFields.includes(item.field_name)}
                handleChange={value => {
                  props.handleClientFieldChange(
                    item.field_name,
                    value,
                    item.input_type,
                  );
                }}
              />,
            );
            break;
          case 1:
            columnTwoFields.push(
              <HMOClientField
                label={item.system_name}
                fieldname={item.field_name}
                value={
                  editMemberValues && editMemberValues[item.field_name]
                    ? editMemberValues[item.field_name]
                    : ''
                }
                type={item.input_type}
                disabled={!item.is_editable}
                error={errorFields && errorFields.includes(item.field_name)}
                handleChange={value => {
                  props.handleClientFieldChange(
                    item.field_name,
                    value,
                    item.input_type,
                  );
                }}
              />,
            );
            break;
          case 2:
            columnThreeFields.push(
              <HMOClientField
                label={item.system_name}
                fieldname={item.field_name}
                value={
                  editMemberValues && editMemberValues[item.field_name]
                    ? editMemberValues[item.field_name]
                    : ''
                }
                type={item.input_type}
                disabled={!item.is_editable}
                error={errorFields && errorFields.includes(item.field_name)}
                handleChange={value => {
                  props.handleClientFieldChange(
                    item.field_name,
                    value,
                    item.input_type,
                  );
                }}
              />,
            );
            break;
          default:
            break;
        }
      }
    });
  }

  return (
    <Grid
      container
      style={{ paddingLeft: 32, paddingRight: 80, paddingBottom: 80 }}
    >
      <Grid item xs={12} style={{ paddingTop: 16, paddingBottom: 16 }}>
        <Typography className={classes.formtitle} color="textPrimary">
          Edit Member Details
        </Typography>
      </Grid>
      <Grid item xs={12} style={{ paddingBottom: 8 }}>
        <Typography className={classes.formsubtitle} color="textPrimary">
          HMO Information
        </Typography>
      </Grid>
      <Grid item xs={12} md={4} style={{ paddingTop: 0, paddingBottom: 0 }}>
        <HMOFieldItem
          disabled={false}
          error={errorFields && errorFields.includes('first_name')}
          name="First Name"
          value={
            editMemberValues && editMemberValues['first_name']
              ? editMemberValues['first_name']
              : ''
          }
          handleChange={value => {
            props.handleTextChange('first_name', value);
          }}
        />
        <HMOSelectItem
          name="Suffix"
          value={
            editMemberValues && editMemberValues['suffix']
              ? editMemberValues['suffix']
              : ''
          }
          options={suffix_options}
          handleChange={item => {
            props.handleSelectChange('suffix', item, false);
          }}
        />
        <HMOFieldItem disabled={true} name="Member Account No." value="" />
        <HMOFieldItem
          disabled={true}
          name="Effectivity Date"
          value={
            memberData && isValidDate(memberData.effectivity_date)
              ? moment(memberData.effectivity_date).format('MMM DD, YYYY')
              : ''
          }
        />
        <HMOFieldItem disabled={true} name="MBL" value={MBL ? MBL : ''} />
        <HMOSelectItem
          name="Plan Type"
          value={editMemberValues['plan_type']}
          options={plan_type_options}
          handleChange={(item: ISelectItem) => {
            props.handleSelectChange('plan_type', item);
          }}
        />
      </Grid>
      <Grid item xs={12} md={4} style={{ paddingTop: 0, paddingBottom: 0 }}>
        <HMOFieldItem
          disabled={false}
          name="Middle Name"
          value={
            editMemberValues && editMemberValues['middle_name']
              ? editMemberValues['middle_name']
              : ''
          }
          handleChange={value => {
            props.handleTextChange('middle_name', value);
          }}
        />
        <HMOSelectItem
          name="Civil Status"
          value={
            editMemberValues && editMemberValues['civil_status']
              ? editMemberValues['civil_status']
              : ''
          }
          options={cs_options}
          handleChange={item => {
            props.handleSelectChange('civil_status', item, false);
          }}
        />
        <HMOFieldItem
          disabled={true}
          name="Member ID"
          value={memberData ? memberData.member_id : ''}
        />
        <HMOFieldItem disabled={true} name="Contract Renewal Date" value="" />
        <HMOFieldItem disabled={true} name="ABL" value={ABL ? ABL : ''} />
        <HMOFieldItem
          disabled={true}
          name="Member Type"
          value={memberData ? memberData.member_type : ''}
        />
      </Grid>
      <Grid item xs={12} md={4} style={{ paddingTop: 0, paddingBottom: 0 }}>
        <HMOFieldItem
          disabled={false}
          error={errorFields && errorFields.includes('last_name')}
          name="Last Name"
          value={
            editMemberValues && editMemberValues['last_name']
              ? editMemberValues['last_name']
              : ''
          }
          handleChange={value => {
            props.handleTextChange('last_name', value);
          }}
        />
        <HMOSelectItem
          name="Gender"
          value={
            editMemberValues && editMemberValues['gender']
              ? editMemberValues['gender']
              : ''
          }
          options={gender_options}
          handleChange={item => {
            props.handleSelectChange('gender', item);
          }}
        />
        <HMOFieldItem disabled={true} name="Contract" value={contractName} />
        <HMOFieldItem
          disabled={true}
          name="Contract Expiration Date"
          value={
            memberData && isValidDate(memberData.termination_date)
              ? moment(memberData.termination_date).format('MMM DD, YYYY')
              : ''
          }
        />
        <HMOFieldItem disabled={true} name="PEC" value={PEC ? PEC : ''} />
        <HMOFieldItem disabled={true} name="Batch Process No." value="" />
      </Grid>
      <Grid item xs={12} md={4} style={{ paddingTop: 0, paddingBottom: 0 }}>
        {columnOneFields}
      </Grid>
      <Grid item xs={12} md={4} style={{ paddingTop: 0, paddingBottom: 0 }}>
        {columnTwoFields}
      </Grid>
      <Grid item xs={12} md={4} style={{ paddingTop: 0, paddingBottom: 0 }}>
        {columnThreeFields}
      </Grid>
      <Grid item xs={12} md={4} style={{ paddingTop: 0, paddingBottom: 0 }}>
        <HMOCheckboxItem
          name="VIP Member"
          checked={editMemberValues['is_vip']}
          handleChange={() => {
            props.handleCheckboxChange('is_vip');
          }}
        />
      </Grid>
      <Grid item xs={12} md={4} style={{ paddingTop: 0, paddingBottom: 0 }}>
        <HMOCheckboxItem
          name="Philhealth Rider"
          checked={editMemberValues['is_philhealth_rider']}
          handleChange={() => {
            props.handleCheckboxChange('is_philhealth_rider');
          }}
        />
      </Grid>
      <Grid
        item
        xs={12}
        md={4}
        style={{ paddingTop: 0, paddingBottom: 0 }}
      ></Grid>
      <Grid item xs={12} style={{ paddingTop: 24, paddingBottom: 16 }}>
        <Typography className={classes.formsubtitle} color="textPrimary">
          Dependent Information
        </Typography>
      </Grid>
      <Grid item xs={12} md={12}>
        {Object.keys(dependents).length > 0 ? (
          dependentForms
        ) : (
          <Typography
            className={classes.formdesc}
            style={{ paddingTop: 8, paddingBottom: 8 }}
            color="textPrimary"
          >
            No enrolled dependents.
          </Typography>
        )}
      </Grid>
    </Grid>
  );
};

EditHMOInformation.defaultProps = {
  memberData: {},
  contractName: '',
  MBL: '',
  ABL: '',
  PEC: '',
};
