import * as React from 'react';
import { useState, useEffect, useRef } from 'react';
import {
  Grid,
  Typography,
  Paper,
  Popper,
  ClickAwayListener,
  Box,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
} from '@material-ui/core/';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUserCog } from '@fortawesome/free-solid-svg-icons';
import moment from 'moment';
import { toLower } from 'lodash';

const hmoitemStyles = makeStyles({
  hmoname: {
    fontSize: '0.75rem',
    color: 'rgba(39, 46, 76, 0.6)',
    textTransform: 'uppercase',
    paddingRight: 32,
  },
  hmovalue: {
    fontSize: '1rem',
    fontWeight: 600,
    color: 'rgb(39, 46, 76)',
  },
  hmovaluelink: {
    fontSize: '1rem',
    fontWeight: 600,
    color: 'rgb(39, 46, 76)',
    textDecoration: 'underline',
    lineHeight: 1.5,
  },
});

interface HMOItemProps {
  name?: string;
  value?: string;
  link?: string;
}

const HMOItem: React.FC<HMOItemProps> = (props: HMOItemProps): JSX.Element => {
  const classes = hmoitemStyles(props);

  return (
    <Grid
      container
      alignItems="center"
      style={{ paddingTop: 4, paddingBottom: 4 }}
    >
      <Grid item xs={5}>
        <Typography className={classes.hmoname}>{props.name}</Typography>
      </Grid>
      <Grid item xs={7}>
        {props.link ? (
          <Link to={props.link} className={classes.hmovaluelink}>
            {props.value}
          </Link>
        ) : (
          <Typography className={classes.hmovalue}>{props.value}</Typography>
        )}
      </Grid>
    </Grid>
  );
};

interface HMOInformationProps {
  memberData?: any;
  handleShowModal: (status: string) => void;
  dependents: any[];
  principal: any;
  clientData?: any;
  contractId?: string;
  contractName?: string;
  MBL?: string;
  ABL?: string;
  PEC?: string;
  clientFields?: any[];
}

const hmoinfoStyles = makeStyles(theme => ({
  subtitle: {
    fontSize: '1.125rem',
    fontWeight: 600,
  },
  hmocheckboxlabel: {
    fontSize: '0.75rem',
    color: 'rgb(39, 46, 76)',
  },
  hmographsubtitle: {
    fontSize: '0.75rem',
    textTransform: 'uppercase',
    color: 'rgb(39, 46, 76, 0.6)',
    marginBottom: 16,
  },
  hmodependentsbox: {
    height: 175,
    borderRadius: 0,
    overflow: 'auto',
    boxShadow: '0px 3px 6px #272E4C19',
    padding: 0,
  },
  hmoprincipalsbox: {
    borderRadius: 0,
    overflow: 'auto',
    boxShadow: '0px 3px 6px #272E4C19',
    padding: 0,
  },
  hmoremarksbox: {
    height: 175,
    borderRadius: 8,
    backgroundColor: '#FFFFFF',
    overflow: 'hidden',
    border: '1px solid #272E4C4D',
  },
  hmodependentsbox_link: {
    color: 'rgb(39, 46, 76)',
  },
  hmoprincipalsbox_link: {
    color: 'rgb(39, 46, 76)',
  },
  change_status_link: {
    color: '#0D5E40',
    cursor: 'pointer',
    fontSize: '13px',
    textDecoration: 'underline',
  },
  icons: {
    color: '#0D5E40',
    cursor: 'pointer',
    fontSize: '13px',
  },
  popper: {
    top: '4px',
    '&[x-placement*="bottom"] $arrow': {
      top: 0,
      left: 0,
      marginTop: '-0.9em',
      width: '3em',
      height: '1em',
      '&::before': {
        borderWidth: '0 1em 1em 1em',
        borderColor: `transparent transparent ${theme.palette.background.paper} transparent`,
      },
    },
    '&[x-placement*="top"] $arrow': {
      bottom: 0,
      left: 0,
      marginBottom: '-0.9em',
      width: '3em',
      height: '1em',
      '&::before': {
        borderWidth: '1em 1em 0 1em',
        borderColor: `${theme.palette.background.paper} transparent transparent transparent`,
      },
    },
    '&[x-placement*="right"] $arrow': {
      left: 0,
      marginLeft: '-0.9em',
      height: '3em',
      width: '1em',
      '&::before': {
        borderWidth: '1em 1em 1em 0',
        borderColor: `transparent ${theme.palette.background.paper} transparent transparent`,
      },
    },
    '&[x-placement*="left"] $arrow': {
      right: 0,
      marginRight: '-0.9em',
      height: '3em',
      width: '1em',
      '&::before': {
        borderWidth: '1em 0 1em 1em',
        borderColor: `transparent transparent transparent ${theme.palette.background.paper}`,
      },
    },
  },
  arrow: {
    position: 'absolute',
    fontSize: 7,
    width: '3em',
    height: '3em',
    '&::before': {
      content: '""',
      margin: 'auto',
      display: 'block',
      width: 0,
      height: 0,
      borderStyle: 'solid',
    },
  },
  paper: {
    padding: '15px',
    width: 110,
  },
  status: {
    padding: '5px',
    cursor: 'pointer',
  },
}));

export const HMOInformation: React.FC<HMOInformationProps> = (
  props: HMOInformationProps,
): JSX.Element => {
  const classes = hmoinfoStyles(props);
  const {
    memberData,
    dependents,
    principal,
    clientData,
    contractId,
    contractName,
    MBL,
    ABL,
    PEC,
  } = props;
  const anchorRef = useRef(null);
  const [arrowRef, setArrowRef] = useState<any>(null);
  const [isPopoverOpen, setPopupoverOpen] = useState(false);
  const [statusOptions, setStatusOptions] = useState<any[]>([]);

  const getStatusOptions = () => {
    let options: any[] = [];
    if (props.memberData) {
      const { member_status } = props.memberData;
      switch (toLower(member_status)) {
        case toLower('Active'):
          options = [
            <Grid key={`status-option-suspended`} item xs={12}>
              <Typography
                className={classes.status}
                onClick={openSuspendMemberModal}
              >
                Suspend
              </Typography>
            </Grid>,
            <Grid key={`status-option-terminated`} item xs={12}>
              <Typography
                className={classes.status}
                onClick={openTerminatedMemberModal}
              >
                Terminate
              </Typography>
            </Grid>,
          ];
          break;
        case toLower('Suspended'):
          options = [
            <Grid key={`status-option-active`} item xs={12}>
              <Typography
                className={classes.status}
                onClick={openActiveMemberModal}
              >
                Activate
              </Typography>
            </Grid>,
            <Grid key={`status-option-terminated`} item xs={12}>
              <Typography
                className={classes.status}
                onClick={openTerminatedMemberModal}
              >
                Terminate
              </Typography>
            </Grid>,
          ];
          break;
        case toLower('Terminated'):
          options = [
            <Grid key={`status-option-active`} item xs={12}>
              <Typography
                className={classes.status}
                onClick={openActiveMemberModal}
              >
                Activate
              </Typography>
            </Grid>,
            <Grid key={`status-option-suspended`} item xs={12}>
              <Typography
                className={classes.status}
                onClick={openSuspendMemberModal}
              >
                Suspend
              </Typography>
            </Grid>,
          ];
          break;

        default:
          options = [
            <Grid key={`status-option-active`} item xs={12}>
              <Typography
                className={classes.status}
                onClick={openActiveMemberModal}
              >
                Activate
              </Typography>
            </Grid>,
            <Grid key={`status-option-suspended`} item xs={12}>
              <Typography
                className={classes.status}
                onClick={openSuspendMemberModal}
              >
                Suspend
              </Typography>
            </Grid>,
            <Grid key={`status-option-terminated`} item xs={12}>
              <Typography
                className={classes.status}
                onClick={openTerminatedMemberModal}
              >
                Terminate
              </Typography>
            </Grid>,
          ];
          break;
      }

      setStatusOptions(options);
    }
  };

  function openPopover() {
    // setSuspendMemberModalOpen(true);
    setPopupoverOpen(true);
  }

  function closePopover() {
    setPopupoverOpen(false);
  }

  function openActiveMemberModal() {
    setPopupoverOpen(false);
    props.handleShowModal('active');
  }

  function openSuspendMemberModal() {
    setPopupoverOpen(false);
    props.handleShowModal('suspend');
  }

  function openTerminatedMemberModal() {
    setPopupoverOpen(false);
    props.handleShowModal('terminate');
  }

  function isValidDate(date: string) {
    const d = new Date(date);
    return d instanceof Date && !isNaN(d.getTime());
  }

  let leftsideCF: any[] = [];
  let rightsideCF: any[] = [];
  if (props.clientFields) {
    if (props.clientFields.length > 2) {
      let halfwayThrough = Math.floor(props.clientFields.length / 2);
      leftsideCF = props.clientFields.slice(0, halfwayThrough).map(item => {
        let value = '';
        if (
          item.field_name === 'registered_name' ||
          item.field_name === 'name_on_card'
        ) {
          value =
            clientData && clientData[item.field_name]
              ? clientData[item.field_name]
              : '';
        } else if (item.input_type === 'date') {
          value =
            memberData && memberData[item.field_name]
              ? moment(memberData[item.field_name]).format('MMM DD, YYYY')
              : '';
        } else {
          value =
            memberData && memberData[item.field_name]
              ? memberData[item.field_name]
              : '';
        }
        return <HMOItem name={item.system_name} value={value ? value : '-'} />;
      });
      rightsideCF = props.clientFields
        .slice(halfwayThrough, props.clientFields.length)
        .map(item => {
          let value = '';
          if (
            item.field_name === 'registered_name' ||
            item.field_name === 'name_on_card'
          ) {
            value =
              clientData && clientData[item.field_name]
                ? clientData[item.field_name]
                : '';
          } else if (item.input_type === 'date') {
            value =
              memberData && memberData[item.field_name]
                ? moment(memberData[item.field_name]).format('MMM DD, YYYY')
                : '';
          } else {
            value =
              memberData && memberData[item.field_name]
                ? memberData[item.field_name]
                : '';
          }
          return (
            <HMOItem name={item.system_name} value={value ? value : '-'} />
          );
        });
    } else if (props.clientFields.length > 0) {
      leftsideCF = [...props.clientFields].map(item => {
        let value = '';
        if (
          item.field_name === 'registered_name' ||
          item.field_name === 'name_on_card'
        ) {
          value =
            clientData && clientData[item.field_name]
              ? clientData[item.field_name]
              : '';
        } else if (item.input_type === 'date') {
          value =
            memberData && memberData[item.field_name]
              ? moment(memberData[item.field_name]).format('MMM DD, YYYY')
              : '';
        } else {
          value =
            memberData && memberData[item.field_name]
              ? memberData[item.field_name]
              : '';
        }
        return <HMOItem name={item.system_name} value={value ? value : '-'} />;
      });
    }
  }

  useEffect(() => {
    getStatusOptions();
  }, [props.memberData]);

  return (
    <Grid container style={{ paddingLeft: 32 }}>
      <Grid item xs={12} style={{ paddingTop: 16, paddingBottom: 16 }}>
        <Grid container direction="row" justify="space-between">
          <Grid item xs={8}>
            <Typography className={classes.subtitle} color="textPrimary">
              {clientData && clientData['registered_name']
                ? clientData['registered_name']
                : ''}
            </Typography>
          </Grid>
          <Grid item xs={4}>
            <Grid container direction="row" justify="flex-end">
              <Grid item>
                <span
                  ref={anchorRef}
                  className={classes.change_status_link}
                  onClick={openPopover}
                >
                  <FontAwesomeIcon icon={faUserCog} className={classes.icons} />{' '}
                  Change Status
                  <Popper
                    open={isPopoverOpen}
                    anchorEl={anchorRef.current}
                    placement={'bottom-end'}
                    className={classes.popper}
                    disablePortal={false}
                    style={{ marginTop: '4px' }}
                    modifiers={{
                      flip: {
                        enabled: false,
                      },
                      preventOverflow: {
                        enabled: true,
                        boundariesElement: 'undefined',
                      },
                      arrow: {
                        enabled: true,
                        element: arrowRef,
                      },
                    }}
                  >
                    <span className={classes.arrow} ref={setArrowRef} />
                    <Paper className={classes.paper}>
                      <ClickAwayListener onClickAway={closePopover}>
                        <Grid container>{statusOptions}</Grid>
                      </ClickAwayListener>
                    </Paper>
                  </Popper>
                </span>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12} md={6} style={{ paddingTop: 0, paddingBottom: 0 }}>
        <HMOItem name="Member Account No." value="-" />
        <HMOItem
          name="Member ID"
          value={
            memberData && memberData.member_id ? memberData.member_id : '-'
          }
        />
        <HMOItem
          name="Contract"
          value={contractName ? contractName : '-'}
          link={
            contractId
              ? `/membership/member-contract/${memberData['_id']}/${contractId}`
              : '#'
          }
        />
        <HMOItem
          name="Effectivity Date"
          value={
            memberData && isValidDate(memberData.effectivity_date)
              ? moment(memberData.effectivity_date).format('MMM DD, YYYY')
              : '-'
          }
        />
        <HMOItem name="Contract Renewal Date" value="-" />
        <HMOItem
          name="Contract Expiration Date"
          value={
            memberData && isValidDate(memberData.termination_date)
              ? moment(memberData.termination_date).format('MMM DD, YYYY')
              : '-'
          }
        />
        <HMOItem
          name="Date Printed"
          value={
            memberData && isValidDate(memberData.date_printed)
              ? moment(memberData.date_printed).format('MMM DD, YYYY')
              : '-'
          }
        />
        <HMOItem
          name="VIP Member"
          value={memberData ? (memberData.is_vip ? 'Yes' : 'No') : '-'}
        />
      </Grid>
      <Grid item xs={12} md={6} style={{ paddingTop: 0, paddingBottom: 0 }}>
        <HMOItem
          name="Type"
          value={
            memberData && memberData.member_type ? memberData.member_type : '-'
          }
        />
        <HMOItem
          name="Plan Type"
          value={
            memberData && memberData.plan_type ? memberData.plan_type : '-'
          }
        />
        <HMOItem name="MBL" value={MBL ? MBL : '-'} />
        <HMOItem name="ABL" value={ABL ? ABL : '-'} />
        <HMOItem name="PEC" value={PEC ? PEC : '-'} />
        <HMOItem
          name="Philhealth Rider"
          value={
            memberData ? (memberData.is_philhealth_rider ? 'Yes' : 'No') : '-'
          }
        />
        <HMOItem name="Batch Process No." value="-" />
        <HMOItem
          name="Member Consent"
          value={memberData ? (memberData.member_consent ? 'Yes' : 'No') : '-'}
        />
      </Grid>
      <Grid item xs={12} md={6} style={{ paddingTop: 32, paddingBottom: 0 }}>
        <HMOItem
          name="Date of Birth"
          value={
            memberData && isValidDate(memberData.date_of_birth)
              ? moment(memberData.date_of_birth).format('MMM DD, YYYY')
              : '-'
          }
        />
        <HMOItem
          name="Age"
          value={
            memberData && isValidDate(memberData.date_of_birth)
              ? '' + moment().diff(memberData.date_of_birth, 'years', false)
              : '-'
          }
        />
        <HMOItem name="Gender" value={memberData ? memberData.gender : '-'} />
      </Grid>
      <Grid item xs={12} md={6} style={{ paddingTop: 0, paddingBottom: 0 }}>
        <HMOItem
          name="Civil Status"
          value={memberData ? memberData.civil_status : '-'}
        />
        <HMOItem
          name="Title/Prefix"
          value={memberData && memberData.title ? memberData.title : '-'}
        />
        <HMOItem
          name="Suffix"
          value={memberData && memberData.suffix ? memberData.suffix : '-'}
        />
      </Grid>
      {memberData && memberData.member_type === 'Principal' ? (
        <Grid
          item
          xs={12}
          md={6}
          style={{ paddingTop: 48, paddingBottom: 48, paddingRight: 16 }}
        >
          <Typography className={classes.hmographsubtitle}>
            Enrolled Dependents
          </Typography>
          <Paper className={classes.hmodependentsbox}>
            <Table size="small">
              <TableHead>
                <TableRow>
                  <TableCell>Dependent Name</TableCell>
                  <TableCell>Relationship</TableCell>
                  <TableCell>Status</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {dependents && dependents.length > 0 ? (
                  dependents.map((row, index) => (
                    <TableRow key={index}>
                      <TableCell component="th" scope="row">
                        <Link
                          to={'#'}
                          className={classes.hmodependentsbox_link}
                        >
                          {row['first_name'] +
                            ' ' +
                            (row['middle_name']
                              ? row['middle_name'].charAt(0) + '. '
                              : '') +
                            row['last_name']}
                        </Link>
                      </TableCell>
                      <TableCell>{row['relationship_to_principal']}</TableCell>
                      <TableCell>{row['member_status']}</TableCell>
                    </TableRow>
                  ))
                ) : (
                  <TableRow>
                    <TableCell
                      component="th"
                      scope="row"
                      style={{ textAlign: 'center' }}
                      colSpan={3}
                    >
                      No enrolled dependents
                    </TableCell>
                  </TableRow>
                )}
              </TableBody>
            </Table>
          </Paper>
        </Grid>
      ) : memberData && memberData.member_type === 'Dependent' ? (
        <Grid
          item
          xs={12}
          md={6}
          style={{ paddingTop: 48, paddingBottom: 48, paddingRight: 16 }}
        >
          <Typography className={classes.hmographsubtitle}>
            Principals
          </Typography>
          <Paper className={classes.hmoprincipalsbox}>
            <Table size="small">
              <TableHead>
                <TableRow>
                  <TableCell>Principal Name</TableCell>
                  <TableCell>Relationship</TableCell>
                  <TableCell>Status</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {memberData.principal_name &&
                memberData.principal_name !== '' &&
                memberData.principal_temp_id &&
                memberData.principal_temp_id !== '' ? (
                  <TableRow>
                    <TableCell component="th" scope="row">
                      <Link to={'#'} className={classes.hmoprincipalsbox_link}>
                        {(memberData.principal_first_name
                          ? memberData.principal_first_name
                          : '') +
                          ' ' +
                          (memberData.principal_middle_name
                            ? memberData.principal_middle_name.charAt(0) + '. '
                            : '') +
                          (memberData.principal_last_name
                            ? ' ' + memberData.principal_last_name
                            : '') +
                          (memberData.principal_suffix
                            ? ' ' + memberData.principal_suffix
                            : '')}
                      </Link>
                    </TableCell>
                    <TableCell>
                      {memberData.relationship_to_principal}
                    </TableCell>
                    <TableCell>
                      {principal && principal.length > 0
                        ? principal[0].member_status
                        : ''}
                    </TableCell>
                  </TableRow>
                ) : (
                  <TableRow>
                    <TableCell
                      component="th"
                      scope="row"
                      style={{ textAlign: 'center' }}
                      colSpan={3}
                    >
                      No enrolled principal
                    </TableCell>
                  </TableRow>
                )}
              </TableBody>
            </Table>
          </Paper>
        </Grid>
      ) : null}

      <Grid item xs={12} md={6} style={{ paddingTop: 48, paddingBottom: 48 }}>
        <Typography className={classes.hmographsubtitle}>Remarks</Typography>
        <Box className={classes.hmoremarksbox}></Box>
      </Grid>

      <Grid item xs={12} md={6} style={{ paddingTop: 0, paddingBottom: 0 }}>
        {leftsideCF}
      </Grid>
      <Grid item xs={12} md={6} style={{ paddingTop: 0, paddingBottom: 0 }}>
        {rightsideCF}
      </Grid>
    </Grid>
  );
};

HMOInformation.defaultProps = {
  memberData: {},
  dependents: [],
  clientData: {},
  contractId: '',
  contractName: '',
  MBL: '',
  ABL: '',
  PEC: '',
  clientFields: [],
};
