import * as React from 'react';
import { Grid } from '@material-ui/core';
import { API } from 'Pages/API';
import { PNTable } from './PNTable';

interface IProps {
    memberId: string;
    contractId: string;
    setIsLoading: (isLoading: boolean) => void;
  }
  
  interface IStates {
    response: any;
    // allMemberBenefits: PartialBenefitData[];
    // filteredMemberBenefits: PartialBenefitData[];
    globalFilter: string;
    columnFilters: any;
  }
  
  export class PartnerNetworks extends React.Component<IProps, IStates> {
    constructor(props: IProps) {
      super(props);
  
      this.state = {
        response: null,
        // allMemberBenefits: [],
        // filteredMemberBenefits: [],
        globalFilter: '',
        columnFilters: [],
      };
    }
  
    componentDidMount() {
      const { memberId } = this.props;
      if (memberId && memberId.length > 0) {
        this.loadBenefitsData();
      }
    }
  
    componentDidUpdate(prevProps) {
      if (this.props.memberId !== prevProps.memberId) {
        this.loadBenefitsData();
      }
    }
  
    loadBenefitsData(): void {
      const { memberId, contractId, setIsLoading } = this.props;
      if (!memberId || memberId.length === 0) {
        return;
      }
  
      if (setIsLoading) {
        setIsLoading(true);
      }
      API.findMemberBenefitsByContact(memberId, contractId).then(response => {
        if (response && response.error === undefined) {
          this.setState(
            {
              response: response,
            },
            () => {
              if (setIsLoading) {
                setIsLoading(false);
              }
            },
          );
        } else {
          if (setIsLoading) {
            setIsLoading(false);
          }
        }
      });
    }
  
    render(): JSX.Element {
      const { response } = this.state;
      return (
        // <div className="MemberBenefitsPage section">
          <Grid container className="sub-section" spacing={5}>
            <Grid item xs={12}>
              <PNTable memberBenefits={response} />
            </Grid>
          </Grid>
        //   <div className="section" />
        // </div>
      );
    }
  }
  