  updateMemberDocuments = (uploadedDoc: any) => {
    const memberId = this.props.match.params.member_id;
    const { memberData } = this.state;
    let memberDocuments = [uploadedDoc];
    if (memberData && memberData.documents && memberData.documents.length > 0) {
      memberDocuments = [...memberData.documents, uploadedDoc];
    }

    const memberDataParam = Object.assign(memberData, {
      documents: memberDocuments,
    });

    API.patchMemberData(memberId, memberDataParam)
      .then(patchResponse => {
        console.log('patchMemberData', patchResponse);
      })
      .catch(e => {
        console.log('patchMemberData', e);
      });
  };

  handleFileType = (url: string) => {
    let fileExtension = url.substring(url.length - 4);
    if (fileExtension.includes('pdf')) {
      return 'pdf';
    } else if (fileExtension.includes('doc')) {
      return 'doc';
    } else {
      return 'others';
    }
  };

