  handleClickCell = (data: any) => {
    if (Object.keys(data).length > 0) {
      this.setLoading(true);
      const { documents_all_data } = this.state;
      _.forEach(documents_all_data, (temp_data, cnt) => {
        if (temp_data['_id'] === data['id']) {
          if (data['name'] === 'required') {
            documents_all_data[cnt]['required'] =
              data['value'] === 'true' || data['value'] === true;
          } else if (data['name'] === 'action') {
            if (
              documents_all_data[cnt]['files'] &&
              documents_all_data[cnt]['files'] !== undefined &&
              documents_all_data[cnt]['files'] !== null
            ) {
              documents_all_data[cnt]['files'].push(data['value']);
            } else {
              documents_all_data[cnt]['files'] = [];
              documents_all_data[cnt]['files'].push(data['value']);
            }
          }

          return false;
        }

        return true;
      });

      this.generateDocumentData(documents_all_data, false);
      this.setLoading(false);
    }
  };

  handleSaveDocument = () => {
    const { documents_all_data: uploadedDocumentsList } = this.state;
    const documentsPatchData: any[] = [];
    if (uploadedDocumentsList && uploadedDocumentsList.length > 0) {
      _.forEach(uploadedDocumentsList, docsList => {
        const { files } = docsList;
        if (files && files.length > 0) {
          documentsPatchData.push(...files);
        }
      });
    }

    this.setState({
      loading: true,
    });

    const memberId = this.props.match.params.member_id;
    const { memberData } = this.state;
    const memberDataParam = Object.assign(memberData, {
      documents: documentsPatchData,
    });

    API.patchMemberData(memberId, memberDataParam)
      .then(patchResponse => {
        console.log('patchMemberData', patchResponse);
        this.setState({
          loading: false,
        });
        if (patchResponse && patchResponse.error === undefined) {
          this.setState({
            loading: false,
            disableSaveBtn: true,
            isOpenModal: true,
            modalTitle: 'Success',
            modalMessage: 'Successfully saved changes on documents..',
          });
        } else {
          this.setState({
            loading: false,
            isOpenModal: true,
            modalTitle: 'Error',
            modalMessage: 'Failed to save documents.',
          });
        }
      })
      .catch(e => {
        console.log('patchMemberData', e);
      });
  };

