  applySortFilter = (data: any, sorting: any, filter: any) => {
    let filtered: any[] = [];
    if (sorting && sorting[0] && sorting[0]['columnName']) {
      switch (sorting[0]['columnName']) {
        case 'name':
          if (sorting[0]['direction'] === 'asc') {
            data.rows.sort((a, b) => (a.name > b.name ? 1 : -1));
          } else {
            data.rows.sort((a, b) => (a.name > b.name ? -1 : 1));
          }
          break;
        case 'purpose':
          if (sorting[0]['direction'] === 'asc') {
            data.rows.sort((a, b) => (a.purpose > b.purpose ? 1 : -1));
          } else {
            data.rows.sort((a, b) => (a.purpose > b.purpose ? -1 : 1));
          }
          break;
        case 'files':
          if (sorting[0]['direction'] === 'asc') {
            data.rows.sort((a, b) => (a.files > b.files ? 1 : -1));
          } else {
            data.rows.sort((a, b) => (a.files > b.files ? -1 : 1));
          }
          break;
      }
    }
    let filter_name: string = '';
    let filter_purpose: string = '';
    let filter_files: string = '';

    filter.forEach(item => {
      if (item.columnName === 'name') filter_name = item.value.toLowerCase();
      if (item.columnName === 'purpose')
        filter_purpose = item.value.toLowerCase();
      if (item.columnName === 'files') filter_files = item.value.toLowerCase();
    });
    if (filter.length > 0) {
      data.rows.forEach(item => {
        if (
          item.name !== undefined &&
          item.name !== null &&
          item.name.toLowerCase().includes(filter_name) &&
          (item.purpose !== undefined &&
            item.purpose !== null &&
            item.purpose.toLowerCase().includes(filter_purpose)) &&
          (item.files !== undefined &&
            item.files !== null &&
            item.files.toLowerCase().includes(filter_files))
        )
          filtered.push(item);
      });
      data.rows = filtered;
    }
    return data;
  };

