  _saveMemberDependents = callback => {
    let ins = this;
    let dependentPayloads = ins.state.editMemberValues['dependents'];

    Object.keys(dependentPayloads).forEach(key => {
      delete dependentPayloads[key]['index'];
    });

    Promise.all(
      API.patchMemberDependents(
        Object.keys(dependentPayloads),
        dependentPayloads,
      ),
    ).then(responses => {
      let errors = responses.filter((error: string) => {
        return error.trim().length > 0;
      });
      callback(errors);
    });
  };

  _hasEditErrors = (callback: any) => {
    let errors = 0;
    let currEditMemberValues = this.state.editMemberValues;
    let currErrorFields = this.state.errorFields;

    //main field errors
    if (
      currEditMemberValues['first_name'] &&
      currEditMemberValues['first_name'].trim()
    ) {
      currErrorFields = currErrorFields.filter(item => item !== 'first_name');
    } else {
      if (currErrorFields.indexOf('first_name') === -1) {
        currErrorFields.push('first_name');
      }
      errors++;
    }

    if (
      currEditMemberValues['last_name'] &&
      currEditMemberValues['last_name'].trim()
    ) {
      currErrorFields = currErrorFields.filter(item => item !== 'last_name');
    } else {
      if (currErrorFields.indexOf('last_name') === -1) {
        currErrorFields.push('last_name');
      }
      errors++;
    }

    // client fields errors
    let clientFields = this.state.clientFields;
    if (clientFields.length > 0) {
      clientFields.forEach(item => {
        let cfkey = item.field_name;
        let cfval = currEditMemberValues[cfkey];
        if (item.input_type === 'date') {
          if (item.is_required) {
            if (cfval && this.isValidDate(cfval)) {
              currErrorFields = currErrorFields.filter(item => item !== cfkey);
            } else {
              if (currErrorFields.indexOf(cfkey) === -1) {
                currErrorFields.push(cfkey);
              }
              errors++;
            }
          } else {
            if (cfval) {
              if (this.isValidDate(cfval)) {
                currErrorFields = currErrorFields.filter(
                  item => item !== cfkey,
                );
              } else {
                if (currErrorFields.indexOf(cfkey) === -1) {
                  currErrorFields.push(cfkey);
                }
                errors++;
              }
            } else {
              currErrorFields = currErrorFields.filter(item => item !== cfkey);
            }
          }
        } else if (item.is_required) {
          if (cfval && cfval.trim()) {
            currErrorFields = currErrorFields.filter(item => item !== cfkey);
          } else {
            if (currErrorFields.indexOf(cfkey) === -1) {
              currErrorFields.push(cfkey);
            }
            errors++;
          }
        }
      });
    }

    // dependent errors
    let dependents = currEditMemberValues['dependents'];
    let dependentsErrorFields = this.state.dependentsErrorFields
      ? this.state.dependentsErrorFields
      : {};

    Object.keys(dependents).forEach(key => {
      let dependent = dependents[key];
      let dependentError: string[] = dependentsErrorFields[key]
        ? dependentsErrorFields[key]
        : [];

      let fn = dependent['first_name'];
      if (fn && fn.trim().length > 0) {
        dependentError = dependentError.filter(item => item !== 'first_name');
      } else {
        if (dependentError.indexOf('first_name') === -1) {
          dependentError.push('first_name');
        }
        errors++;
      }

      let mn = dependent['middle_name'];
      if (mn && mn.trim().length > 0) {
        dependentError = dependentError.filter(item => item !== 'middle_name');
      } else {
        if (dependentError.indexOf('middle_name') === -1) {
          dependentError.push('middle_name');
        }
        errors++;
      }

      let ln = dependent['last_name'];
      if (ln && ln.trim().length > 0) {
        dependentError = dependentError.filter(item => item !== 'last_name');
      } else {
        if (dependentError.indexOf('last_name') === -1) {
          dependentError.push('last_name');
        }
        errors++;
      }

      let bd = dependent['date_of_birth'];
      if (bd && this.isValidDate(bd)) {
        dependentError = dependentError.filter(
          item => item !== 'date_of_birth',
        );
      } else {
        if (dependentError.indexOf('date_of_birth') === -1) {
          dependentError.push('date_of_birth');
        }
        errors++;
      }

      dependentsErrorFields[key] = dependentError;
    });

    this.setState(
      {
        errorFields: currErrorFields,
        dependentsErrorFields: dependentsErrorFields,
      },
      () => {
        callback(errors > 0);
      },
    );
  };

  handleEditSave = () => {
    let ins = this;

    ins._hasEditErrors(hasError => {
      if (hasError) {
        ins.setState({
          loading: false,
          isOpenModal: true,
          modalTitle: 'Error',
          modalMessage: 'Please fill out the required fields.',
        });
        return;
      }

      //submit
      let memberData = ins.state.memberData;
      let editMemberValues = ins.state.editMemberValues;

      let payload = {
        last_name: editMemberValues['last_name']
          ? editMemberValues['last_name']
          : memberData['last_name'],
        middle_name: editMemberValues['middle_name']
          ? editMemberValues['middle_name']
          : memberData['middle_name'],
        first_name: editMemberValues['first_name']
          ? editMemberValues['first_name']
          : memberData['first_name'],
        suffix: editMemberValues['suffix']
          ? editMemberValues['suffix']
          : memberData['suffix'],
        civil_status: editMemberValues['civil_status']
          ? editMemberValues['civil_status']
          : memberData['civil_status'],
        gender: editMemberValues['gender']
          ? editMemberValues['gender']
          : memberData['gender'],
        date_of_birth: memberData['date_of_birth'],
        plan_type: editMemberValues['plan_type'],
        is_vip: editMemberValues['is_vip'],
        is_philhealth_rider: editMemberValues['is_philhealth_rider'],
        contracts: memberData['contracts'],
      };

      let clientFields = this.state.clientFields;
      if (clientFields.length > 0) {
        clientFields.forEach(item => {
          if (
            item.field_name !== 'registered_name' &&
            item.field_name !== 'name_on_card'
          ) {
            if (item.input_type === 'date') {
              payload[item.field_name] = editMemberValues[item.field_name]
                ? new Date(editMemberValues[item.field_name]).toISOString()
                : '';
            } else {
              payload[item.field_name] = editMemberValues[item.field_name]
                ? editMemberValues[item.field_name]
                : '';
            }
          }
        });
      }

      ins.setState(
        {
          loading: true,
        },
        () => {
          API.patchMemberData(ins.props.match.params.member_id, payload).then(
            response => {
              if (response.ok) {
                ins._saveMemberDependents(errors => {
                  if (errors.length > 0) {
                    let errorMsg = errors.join('; ');

                    ins.setState({
                      loading: false,
                      isOpenModal: true,
                      modalTitle: "Error saving dependents' information",
                      modalMessage: errorMsg,
                    });
                  } else {
                    ins.loadMemberInformation(isSuccess => {
                      if (isSuccess) {
                        ins.setState({
                          sideNav: 'hmo_information',
                          edit_mode: false,
                          editMemberValues: defaultEditMemberValues,
                          errorFields: [],
                          dependentsErrorFields: {},
                          isOpenModal: true,
                          modalTitle: 'Success',
                          modalMessage: "Successfully updated member's data",
                        });
                      } else {
                        ins.setState({
                          isOpenModal: true,
                          modalTitle: 'Error fetching new information',
                          modalMessage:
                            'An error occurred while fetching updated information.',
                        });
                      }
                    });
                  }
                });
              } else {
                let error = response.error || {};
                ins.setState({
                  loading: false,
                  isOpenModal: true,
                  modalTitle: 'Error saving changes',
                  modalMessage: error.message,
                });
              }
            },
          );
        },
      );
    });
  };

  handleEditCancel = () => {
    this.setState({
      sideNav: 'hmo_information',
      edit_mode: false,
      editMemberValues: defaultEditMemberValues,
      errorFields: [],
      dependentsErrorFields: {},
    });
  };

  handleEditTextChange = (key: string, value: string) => {
    let currEditMemberValues = this.state.editMemberValues;
    let memberData = this.state.memberData;
    let currVal = memberData[key];

    switch (key) {
      case 'first_name':
      case 'middle_name':
      case 'last_name':
        currEditMemberValues[key] = value && value.trim() ? value : currVal;

        let currDependents = currEditMemberValues['dependents'];
        let currDependentsKeys = Object.keys(currDependents);
        if (currDependentsKeys.length > 0) {
          let newPrincipalName = this._generatePrincipalName(
            currEditMemberValues,
          );
          currDependentsKeys.forEach(dependentKey => {
            currDependents[dependentKey]['principal_' + key] = value;
            currDependents[dependentKey]['principal_name'] = newPrincipalName;
          });
          currEditMemberValues['dependents'] = currDependents;
        }

        this.setState({
          editMemberValues: currEditMemberValues,
        });
        break;
      default:
        currEditMemberValues[key] = value && value.trim() ? value.trim() : '';
        this.setState({
          editMemberValues: currEditMemberValues,
        });
        break;
    }
  };

  handleEditClientFieldChange = (
    fieldname: string,
    value: string,
    type: string,
  ) => {
    let currEditMemberValues = this.state.editMemberValues;
    let currErrorFields = this.state.errorFields;

    if (type === 'date') {
      currEditMemberValues[fieldname] = value ? value : '';
    } else {
      currEditMemberValues[fieldname] =
        value && value.trim() ? value.trim() : '';
    }
    currEditMemberValues[fieldname] = value ? value : '';
    this.setState({
      errorFields: currErrorFields,
      editMemberValues: currEditMemberValues,
    });
  };

  handleEditSelectChange = (key: string, item: ISelectItem) => {
    let currEditMemberValues = this.state.editMemberValues;

    switch (key) {
      case 'suffix':
        currEditMemberValues[key] = item.value;

        let currDependents = currEditMemberValues['dependents'];
        let currDependentsKeys = Object.keys(currDependents);
        if (currDependentsKeys.length > 0) {
          let newPrincipalName = this._generatePrincipalName(
            currEditMemberValues,
          );
          currDependentsKeys.forEach(dependentKey => {
            currDependents[dependentKey]['principal_' + key] = item.value;
            currDependents[dependentKey]['principal_name'] = newPrincipalName;
          });
          currEditMemberValues['dependents'] = currDependents;
        }

        this.setState({
          editMemberValues: currEditMemberValues,
        });
        break;
      default:
        currEditMemberValues[key] = item.value;
        this.setState({
          editMemberValues: currEditMemberValues,
        });
        break;
    }
  };

  handleEditCheckboxChange = (key: string) => {
    let currEditMemberValues = this.state.editMemberValues;
    let currVal = currEditMemberValues[key];
    currEditMemberValues[key] = !currVal;
    this.setState({
      editMemberValues: currEditMemberValues,
    });
  };

  handleDependentsChange = (
    key: string,
    fieldName: string,
    fieldValue: string,
  ) => {
    let currEditMemberValues = this.state.editMemberValues;
    if (fieldName === 'date_of_birth') {
      currEditMemberValues['dependents'][key][fieldName] = new Date(
        fieldValue,
      ).toISOString();
    } else {
      currEditMemberValues['dependents'][key][fieldName] = fieldValue;
    }
    this.setState({
      editMemberValues: currEditMemberValues,
    });
  };

